import {playlistActions} from '../actionTypes/actionTypes'

const initialState = {
	fetching: true,
}

export default function playlist(state = initialState, action){
	switch(action.type){
		case playlistActions.UPDATE_LIST_REQUEST:
			return {...state, fetching: true}

		case playlistActions.UPDATE_LIST_SUCCESS:
			return {...state, ...action.data, fetching: false}

		case playlistActions.UPDATE_LIST_ERROR:
			return state

		default:
			return state
	}
}


