import {playerActions} from '../actionTypes/actionTypes'

const initialState = {
	fetching: true,
	time:"00:00"
}

export default function player(state = initialState, action){
	switch(action.type){
		case playerActions.GET_NEXT_TRACK_REQUEST:
			return {...state, 
				fetching: true,
				time:"00:00"
			}

		case playerActions.GET_NEXT_TRACK_SUCCESS:
			const start = new Date()
			return {...state, ...action.data,
				fetching: false,
				start: start
			}

		case playerActions.GET_NEXT_TRACK_ERROR:
			return {...state
			}

		case playerActions.UPDATE_TIME:
			if(!state.track){
				return {...state}
			}
			const time = (new Date(action.now - state.start + state.offset * 1000)).toTimeString().slice(3,8)
			return {...state, time: time}

		default:
			return state
	}
}



