import React, {Component} from 'react'
import './Track.css'

export default class Track extends Component{
	render(){
		const {
			author,
			title,
			duration,
			id
		} = this.props.data
		const trackLength = new Date(0, 0, 0, 0, 0, duration).toTimeString().slice(3,8)
	
		return (
			<div className="track">
				<div className="label">
					<span className="author">{author}</span>
					<span className="title">{' – ' + title}</span>
				</div>
				<div className="duration">{trackLength}</div>
				<div className="add">+</div>
			</div>
		)
	}
}


