import React, {Component} from 'react'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as pageActions from '../pageActions/pageActions'
import Player from '../components/Player'
import Playlist from '../components/Playlist'
// import api from 'vk-universal-api'


class Root extends Component{
	render(){
		const {getNextTrack, updateTime, updatePlaylist} = this.props.actions
		return (
			<div className="root">
				<Player data={this.props.player} 
					getNextTrack={getNextTrack}
					updateTime={updateTime}
				/>
				<Playlist data={this.props.playlist}
					updatePlaylist={updatePlaylist}
				/>
			</div>
		)
	}
}

const mapStateToProps = (state) => {
	return Object.assign({}, state)
}

const mapDispatchToProps = (dispatch) => {
	return {
		actions: bindActionCreators(pageActions, dispatch)
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Root)


