import {
	playerActions, 
	playlistActions} from '../actionTypes/actionTypes'

export function getNextTrack(){
	return (dispatch) => {
		dispatch({
			type: playerActions.GET_NEXT_TRACK_REQUEST
		})

		fetch("/audio")
		.then((response) => {
			response.json().then((json) => {
				dispatch({
				type: playerActions.GET_NEXT_TRACK_SUCCESS,
				data: json
				})
			})
		})
		.catch((error) => {
			console.log(`Error: ${error.message}`)
			dispatch({
				type: playerActions.GET_NEXT_TRACK_ERROR
			})
		})
	}
}

export function updateTime(){
	const now = new Date()
	return{
		type: playerActions.UPDATE_TIME,
		now: now
	}
}

export function updatePlaylist(){
	return (dispatch) => {
		dispatch({
			type: playlistActions.UPDATE_LIST_REQUEST
		})

		fetch("/playlist")
		.then((response) => {
			response.json().then((json) => {
				dispatch({
				type: playlistActions.UPDATE_LIST_SUCCESS,
				data: json
				})
			})
		})
		.catch((error) => {
			console.log(`Error: ${error.message}`)
			dispatch({
				type: playlistActions.UPDATE_LIST_ERROR
			})
		})
	}
}


