import React, {Component} from 'react'
import Track from './Track'
import './Playlist.css'

export default class Playlist extends Component{
	componentDidMount() {
		this.interval = setInterval(this.props.updatePlaylist(), 10000)
	}
	makeList(){
		const list = this.props.data.playlist.map((elem => {
			return <Track data={elem} />
		}))
		return list
	}
	render(){
		const {
			fetching,
			playlist
			} = this.props.data

		const list = fetching ? 'Loading...' : this.makeList.bind(this)()

		return(
			<div className="playlist">
				{list}
			</div>)
	}
}


