import React, {Component} from 'react'
import ReactAudioPlayer from 'react-audio-player'
import './Player.css'

export default class Player extends Component{
	handleVolumeChange(){
		this.player.audioEl.volume = Math.pow(this.volume.value, 2) * 0.0001
	}
	componentDidMount() {
		this.props.getNextTrack()
		this.interval = setInterval(this.props.updateTime, 1000)
	}
	componentDidUpdate(prevProps, prevState) {
		const p = this.player.audioEl
		if(this.props.data.offset > p.currentTime){
			p.currentTime = this.props.data.offset
			this.props.updateTime()
		}
	}
	toggleMute(){
		const p = this.player.audioEl
		p.muted = !p.muted
		
		const i = this.icon
		if(i.dataset.muted == 'false'){
			i.setAttribute('data-muted', true)
		}else{
			i.setAttribute('data-muted', false)
		}
	}
	render(){

		const {
			track,
			author,
			title,
			fetching,
			time
		} = this.props.data

		return (
			<div className="player">
				<ReactAudioPlayer 
					autoPlay={"true"}
					controls={false}
					ref={e => { this.player = e }}
					src={track}
				/>

				<div className="now-playing">
					<div className="play-icon"
						data-muted={false}
						onClick={this.toggleMute.bind(this)}
						ref={e => {this.icon = e}}
					/>
					<div className="comp-wrapper">
						<div className="composition">
							<span className="now-track">{!fetching ? `${author} – ${title}` : 'Loading...'}</span>
						</div>
					</div>
				</div>
				
				<div className="vol-time-wrapper">
					<div className="track-time">{time}</div>
					<div className="volume">
						<input 
							defaultValue={70}
							type="range"
							min={0}
							max={100}
							ref={e => { this.volume = e }}
							onChange={this.handleVolumeChange.bind(this)}
						/>
					</div>
				</div>

			</div>
		)
	}
}


